.wrapper {
  color: #ffffff;
}

.title {
  display: block;
  font-size: 2rem;
  margin-bottom: 0.5rem;

  &:hover {
    text-decoration: underline;
  }
}

.list {

 }

.item {
  width: 50px;
  display: inline-block;
  text-align: center;

  &:hover {
    text-decoration: underline;
  }


  &:not(:last-of-type) {
    margin-right: 8px;
  }

  &Pic {
    width: 50px;
    height: 50px;
    object-fit: cover;
    border-radius: 50%;
  }

  &Name {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}