@import "../../assets/styles/data";

.sidebar {
  width: 166px;
  padding: 1rem;
  grid-area: n;
  background-color: $main-color;
  border-radius: 2px;
}

.nav {
  margin-bottom: 3rem;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  margin-bottom: 0.2rem;
  color: #ffffff;

  a:hover {
    text-decoration: underline;
  }

  a.active {
    text-decoration: none;
    color: darkslategray;
  }
}