@import "src/assets/styles/data.scss";

.header {
  padding: 5px 20px;
  grid-area: h;
  background-color: $main-color;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  border-radius: 2px;
}

.logout {
  line-height: 18px;

  &:hover {
    text-decoration: underline;
  }
}

.logoutBtn {
  margin-left: 0.5rem;
  margin-top: -2px;
  padding: 0;
  font-size: 0;
  border: 0;
  background: none;
  vertical-align: middle;
  cursor: pointer;
}

.logoutIcon {
  display: block;
  width: 18px;
  height: 18px;
  fill: #ffffff;
}

.logo {
  width: 40px;
  fill: #ffffff;
}