@import "src/assets/styles/data.scss";

html {
  height: 100%;
}

body {
  height: 100%;
  padding: 1rem;
  box-sizing: border-box;
  line-height: 1.4;
  font-family: 'Source Sans Pro', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: darkslategray;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  color: inherit;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}

.app-wrapper {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "h h h" "n c c";
  grid-template-rows: 3.75rem 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 1rem;
}

.app-content {
  grid-area: c;
}

.btn {
  min-width: 94px;
  padding: 8px 16px;
  margin: 0.5rem 0;
  color: #ffffff;
  border: none;
  background-color: $main-color;
  border-radius: 4px;
  cursor: pointer;
  outline: 0;

  &:disabled {
    opacity: 0.2;
  }
}